@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
:root {
  --small-links-color: #b8b8b8;
}

body {
  margin: 0px;
}

.small-nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 40px;
  background-color: #222222;
  font-size: 13px;
  color: #b8b8b8;
  color: var(--small-links-color);
}

.free {
  padding-left: 1.5rem;
}

.free p {
  margin: 10px;
}

ul.help {
  display: flex;
  /* width: 34.5%; */
  margin: 0px;
  list-style-type: none;
}

.small-nav li {
  text-decoration: none;
  border-left: 0.1px solid;
  padding: 11px 32px 0px 35px;
}

.small-links {
  text-decoration: none;
  color: #b8b8b8;
  color: var(--small-links-color);
}

.line {
  border-right: 1px solid;
}

nav.big-nav {
  display: flex;
  justify-content: space-between;
  padding: 16px 20px 16px 30px;
  /* padding: 20px 20px 26px 30px; */
  position: fixed;
  width: 100%;
  z-index: 1100;
}

.big-nav.active {
  background: #ffffff;
  transition: 0.4s;
  margin-top: -40px;
}

.left {
  display: flex;
  width: 70%;
}

ul.pages-links {
  display: flex;
  justify-content: space-around;
  width: 60%;
  padding-left: 2rem;
  list-style-type: none;
}

.big-nav h1 {
  margin: 0px;
  letter-spacing: 3px;
}
.big-nav h1 > span {
  color: #7a7979;
}

.big-links {
  color: #464646;
  text-decoration: none;
}
a.icons {
  font-size: 28px;
  color: #464646;
}

.big-drop {
  position: relative;
  display: block;
}

.home-drop {
  display: none;
  position: absolute;
  background-color: white;
  width: 10rem;
  z-index: 1;
  top: 100%;
}

.home-drop > li {
  list-style: none;
  padding: 11px;
  display: block;
}

.home-drop .drop {
  text-decoration: none;
  color: rgb(133, 133, 133);
  letter-spacing: 1px;
}

.big-links:hover {
  color: var(--btn-color);
}

/* .jux:active {
  background-color: var(--btn-color);
} */

.home-drop .drop:hover {
  color: var(--btn-color);
}

.big-drop:hover .home-drop {
  display: block;
}

.menu-icon {
  display: none;
}

.shopping-icons {
  display: flex;
  justify-content: space-between;
  width: 10%;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Lobster", cursive;
}

.warp {
  position: fixed;
  z-index: 1100;
  width: 100%;
  height: 100vh;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0);
  visibility: hidden;
  transition: all 0.4s;
}

/* FOr media queries*/
/* @media screen and (min-width: 1000px) and (max-width: 1200px) {
  .jus {
    bottom: 8rem;
  }
} */
@media screen and (max-width: 992px) {
  .slider {
    height: 100vh;
  }

  /* .jus {
    bottom: 20rem !important;
  } */

  .small-nav {
    display: none;
  }
  .big-nav.active {
    margin-top: 0px;
  }
  .big-nav {
    position: absolute !important;
    background-color: white;
  }

  .shopping-icons {
    width: 20%;
  }

  .jux {
    padding: 22px 0px;
    width: 15%;
  }

  .big-links {
    color: white;
  }

  .big-links:hover {
    color: #0c1a74;
    transition: ease-in-out 0.5s;
  }

  .menu-icon {
    display: block;
    font-size: 28px;
    color: rgb(0, 0, 0);
    cursor: pointer;
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
  }

  ul.pages-links {
    flex-direction: column;
    width: 100%;
    height: auto;
    position: absolute;
    top: 100%;
    left: 0;
    color: white;
    opacity: 1;
    transition: 0.4s;
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    background-color: var(--btn-color);
  }

  ul.pages-links.active {
    display: block;
    top: 52px;
    z-index: 1000;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  .spring {
    width: 38rem !important;
    margin-bottom: 5rem;
  }
  .all {
    width: 90% !important;
    padding-bottom: 20px;
  }
  .section {
    flex-wrap: wrap !important;
  }
  .filter,
  .second-search {
    margin-left: 0px !important;
    margin-right: 14px;
  }
  .picture {
    padding: 20px 6px !important;
    width: 19rem !important;
  }
  .shop {
    top: 20rem !important;
    bottom: 50px !important;
  }
  .sort,
  .money,
  .color {
    flex: 0 1 45% !important;
  }
  .tags {
    flex: 0 1 46% !important;
  }
}
@media screen and (max-width: 768px) {
  .menu-icon,
  a.icons {
    font-size: 23px;
  }
  .jus-btn {
    padding: 15px 30px !important;
    font-size: 12px;
  }
  .left-arrow,
  .right-arrow {
    font-size: 3rem;
  }
  .categories,
  .hlep,
  .touch,
  .newsletter {
    flex: 0 1 45% !important;
  }
}

@media screen and (max-width: 640px) {
  .btn {
    padding: 9px;
    font-size: 14px;
  }
  .spring {
    margin-bottom: 4rem;
  }
  .shopping-icons {
    width: 30%;
  }
  .picture {
    width: 29rem !important;
  }

  .quick {
    top: 30rem !important;
    left: 10.5rem !important;
  }
  .sort,
  .money,
  .color,
  .tags {
    flex: 0 1 100% !important;
  }
}

@media screen and (max-width: 575px) {
  .jus {
    left: 6rem !important;
    bottom: 11rem !important;
  }
  .jus-btn {
    padding: 12px 20px !important;
    font-size: 10px;
  }

  .btn {
    padding: 7px !important;
    font-size: 13px !important;
  }
  .categories,
  .hlep,
  .touch,
  .newsletter {
    flex: 0 1 93% !important;
  }
}

:root {
  --text-color: #b3b3b3;
}

/* for smallBox */
.card {
  display: flex;
  padding: 3rem 1rem;
  flex-wrap: wrap;
}

.spring {
  width: 26rem;
  border: 1px solid #e3e3e3;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2rem;
  position: relative;
}

.spring img {
  max-width: 100%;
  vertical-align: middle;
}

.spring-link {
  /* display: flex;
  flex-direction: column;
  justify-content: space-between; */
  position: absolute;
  left: 0px;
  top: 0px;
  padding: 38px 34px;
  text-decoration: none;
  width: 100%;
  height: 100%;
  color: black;
}
.spring-text {
  display: flex;
  flex-direction: column;
}

.spring-link:hover {
  background-color: rgba(102, 117, 214, 0.7);
  transition: ease-in-out 0.6s;
}

.spring-link:hover .season,
.spring-link:hover .title {
  color: white;
}

.shop {
  position: absolute;
  top: 13rem;
  left: 37px;
  bottom: 32px;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  color: white;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  border-bottom: 2px solid white;
}

.spring-link:hover .shop {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.title {
  font-size: 30px;
}

.season {
  color: #7b7b7b;
  font-size: 14px;
  line-height: 30px;
}

/* For midBox*/
.product {
  padding: 5rem 0px 140px 0px;
  position: relative;
}

.overview {
  margin-bottom: -40px;
  letter-spacing: 6px;
  padding-left: 27px;
}

.section {
  display: flex;
  justify-content: space-between;
  padding: 0px 33px 20px 23px;
}

/* block diplay */

.first-box {
  padding: 10px 26px;
  /* transform: scaleY(0); */
  visibility: visible;
}
.display {
  /* display: block; */
  padding: 10px 26px;
  /* transform: scaleY(1); */
  display: none;
}

.first-box-wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: #edebeb;
  padding: 27px 40px 37px;
}
.sort,
.money,
.color {
  flex: 0 1 25%;
  padding: 0px 15px 25px 0px;
}

.tags {
  flex: 0 1 20%;
}

.first-box-wrap h3 {
  padding-bottom: 15px;
  color: #343434;
}

.sort ul,
.money ul,
.color ul {
  list-style-type: none;
}

.circle {
  padding-right: 6px;
}

.blue {
  color: blue;
}
.grey {
  color: #b3b3b3;
  color: var(--text-color);
}

.green {
  color: green;
}

.red {
  color: red;
}

.white {
  color: #b3b3b3;
  color: var(--text-color);
}

.tags-buttons button {
  padding: 9px 16px;
  border-radius: 20px;
  color: #b3b3b3;
  color: var(--text-color);
  border: 1px solid #a5a5a5;
  margin: 4px 3px 2px 0px;
}

/*for search */
.search-box {
  padding: 10px 34px;
  position: relative;
}

.search-input {
  width: 100%;
  padding: 20px 31px;
  font-size: 18px;
  border: 1px #d5d9dc solid;
}

.search-btn {
  position: absolute;
  border: none;
  top: 34px;
  left: 50px;
  color: #757575;
  font-size: 16px;
  background-color: transparent;
}

.first-box-wrap a {
  color: #b3b3b3;
  color: var(--text-color);
  text-decoration: none;
  line-height: 30px;
  font-size: 15px;
  letter-spacing: 1px;
}

.btn {
  border: none;
  padding: 10px;
  background-color: white;
  font-size: 15px;
  color: #b3b3b3;
  color: var(--text-color);
}

.all {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 40%;
}

.all-icons {
  display: flex;
  justify-content: space-between;
}

.filter,
.second-search {
  padding: 10px 20px;
  background-color: transparent;
  border: 1px solid #e6e6e6;
  font-size: 14px;
  color: #343434;
  margin-left: 14px;
}

.filter:hover,
.second-search:hover {
  background-color: #717fe0;
  color: white;
  cursor: pointer;
  transition: ease-in-out 0.5s;
}

.filter:hover .fitert,
.second-search:hover .searcht {
  color: white;
  transition: ease-in-out 0.5s;
}

.fitert,
.searcht {
  color: #b3b3b3;
  color: var(--text-color);
  font-size: 15px;
}

/* Products*/

.for-product {
  width: 100%;
  vertical-align: middle;
  transition: ease-in-out 0.5s;
}

.pictures-wrap {
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
}

.picture {
  padding: 20px 15px;
  margin: auto;
  width: 20rem;
  text-decoration: none;
}

.product-img {
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}

.product-img:hover .quick {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
}

.product-img:hover .for-product {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.quick {
  position: absolute;
  text-decoration: none;
  top: 17rem;
  left: 5rem;
  bottom: -65px;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  color: white;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
.quick-btn {
  background-color: white;
  color: black;
  padding: 12px 25px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.quick-btn:hover {
  background-color: black;
  color: white;
  transition: ease-in-out 0.5s;
}

.picture-text {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
}

.picture-left {
  display: flex;
  flex-direction: column;
}
.espirit {
  text-decoration: none;
  color: #999999;
}
.love {
  color: #999999;
}
.price {
  color: #b3b3b3;
  color: var(--text-color);
  line-height: 38px;
}
.load {
  text-align: center;
}
.load-btn {
  background-color: #e6e6e6;
  color: black;
  padding: 12px 25px;
  border: none;
  border-radius: 20px;
}

.load-btn:hover {
  background-color: black;
  color: white;
  transition: ease-in-out 0.5s;
  cursor: pointer;
}

*:root {
  --for-arrow: #a5a5a5;
}

.slider {
  position: relative;
  /* height: 120vh; */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* .slide.active {
  z-index: 998;
  opacity: 0;
  transition: opacity 1000ms ease 0s;
} */

.image {
  width: 100%;
  height: 600px;
  border-radius: 10px;
  top: 0px;
}

.right-arrow {
  position: absolute;
  top: 50%;
  right: 32px;
  font-size: 3.5rem;
  color: var(--for-arrow);
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.left-arrow {
  position: absolute;
  top: 50%;
  left: 32px;
  font-size: 3.5rem;
  color: var(--for-arrow);
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.right-arrow:hover,
.left-arrow:hover {
  color: var(--btn-color);
  transition: ease-in-out 0.4s;
}

.slide-img {
  width: 100%;
  top: 0px;
}

.jus {
  position: absolute;
  left: 7.5rem;
  color: #000;
  bottom: 9rem;
  color: rgb(61, 61, 61);
}

.jus h1 {
  /* font-size: 28px; */
  font-size: clamp(0.5rem, 4vw, 3rem);
  font-weight: 100;
  line-height: 6rem;
}

.jus h2 {
  /* font-size: 66px; */
  font-size: clamp(1rem, 5vw, 4rem);
  padding-bottom: 30px;
}

.jus-btn {
  background-color: var(--btn-color);
  padding: 20px 43px;
  border-radius: 30px;
  border: none;
  color: white;
  cursor: pointer;
  font-weight: 600;
}

.jus-btn:hover {
  background-color: #000;
  transition: ease-in-out 0.5s;
}

:root {
  --li-text-color: #8d8d8d;
  --btn-color: #717fe0;
}

.footer {
  background-color: #222222;
}
.row {
  display: flex;
  color: white;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer-wrapper {
  padding: 75px 10px 32px;
}

.row ul {
  list-style: none;
}

.row li {
  padding-bottom: 10px;
  color: #8d8d8d;
  color: var(--li-text-color);
}

.row h4 {
  padding-bottom: 30px;
  letter-spacing: 3px;
}

.touch {
  width: 25%;
}

.touch p {
  color: #8d8d8d;
  color: var(--li-text-color);
}

.categories,
.hlep,
.touch,
.newsletter {
  padding: 0px 15px 50px;
  flex: 0 1 25%;
}

.newsletter {
  display: flex;
  flex-direction: column;
}

input[type="email"] {
  background-color: transparent;
  border: none;
  font-size: 17px;
  color: #8d8d8d;
  color: var(--li-text-color);
  outline: none;
}

.for-sub {
  padding-top: 25px;
}
.sub {
  padding: 15px 14px;
  border-radius: 20px;
  width: 55%;
  border: none;
  background-color: #717fe0;
  background-color: var(--btn-color);
  color: white;
  cursor: pointer;
  font-weight: 600;
}

.sub:hover {
  background-color: white;
  color: #717fe0;
  color: var(--btn-color);
  transition: ease-in-out 0.4s;
}
.socials {
  display: flex;
  justify-content: space-between;
  padding-top: 27px;
  width: 24%;
  font-size: 20px;
}
.social-icon-link {
  color: #8d8d8d;
  color: var(--li-text-color);
}

.social-icon-link:hover {
  color: #717fe0;
  color: var(--btn-color);
  transition: ease-in-out 0.4s;
}

.bottom {
  text-align: center;
  padding: 5rem 0px 0px;
}

.pay-cards {
  padding-bottom: 25px;
}

.bottom p {
  color: #8d8d8d;
  color: var(--li-text-color);
}

/*for cart */
:root {
  --border-color: #e6e6e6;
}

.cart {
  padding: 6rem 3.5rem;
}
.home {
  text-decoration: none;
  color: #555;
  font-size: 13px;
}

.home:hover {
  color: var(--btn-color);
}

.home-arrow {
  vertical-align: middle;
}

.shopping-cart {
  font-size: 13px;
  color: rgb(163, 163, 163);
}

form {
  padding: 70px 10px;
}
.left-table {
  width: 70%;
}
.left-table-wrap {
  overflow: auto;
  border-left: 1px solid #e6e6e6;
  border-left: 1px solid var(--border-color);
  border-right: 1px solid #e6e6e6;
  border-right: 1px solid var(--border-color);
}
.first-table {
  width: 100%;
  border-collapse: collapse;
}
.table-head {
  border-top: 1px solid #e6e6e6;
  border-top: 1px solid var(--border-color);
  border-bottom: 1px solid #e6e6e6;
  border-bottom: 1px solid var(--border-color);
}

.left-table th {
  font-size: 13px;
  color: #555;
  text-transform: uppercase;
  line-height: 1.6;
  padding-top: 15px;
  padding-bottom: 15px;
}

